.content {
  padding-bottom: 20px;
}
.card {
  margin-bottom: 20px;
  .card-block {
    font-size: .95em;
    line-height: 20px;
  }
  u {
    display: block;
  }
}

.dropdown-menu {
  .dropdown-item {
    padding: 7px 1.5rem;
    color: $clr_4;
    &:active,
    &:hover {
      background: $clr_4;
      color: $clr_1;
      .fa {
        color: $clr_1;
      }
    }
    &:focus {
      background: $clr_4;
      color: $clr_1;
      .fa {
        color: $clr_1;
      }
    }
  }
  .fa {
    margin-right: 7px;
    color: $clr_4;
  }
}

.btn {
  &.btn-primary {
    background: $clr_4;
    border-color: transparent;
    &:active {
      background: $clr_4;
    }
  }
  &:focus {
    box-shadow: none;
  }
}

.show {
  .btn {
    &.btn-primary {
      background: $clr_4;
      border-color: transparent;
      opacity: .7;
    }
  }
}

.link-module {
  display: flex;
  align-items: flex-start;
  background: $clr_1;
  margin-bottom: 15px;
  font-size: .95em;
  img {
    width: 90px;
    margin-right: 10px;
  }
  span {
    span {
      display: block;
    }
  }
}

.carousel {
  .carousel-indicators {
    li {
      background: $clr_6;
      &.active {
        background: $clr_4;
      }
    }
  }
  
  .carousel-item {
    padding: 15px 0 30px;
  }
}

.accordion-module {
  h6 {
    a {
      color: $clr_4;
      display: flex;
      align-items: center;
      img {
        margin-right: 7px;
        width: 16px;
      }
    }
  }
  ul {
    padding-left: 15px;
  }
  li {
    margin-bottom: 7px;
  }
  .fa {
    color: $clr_4;
    font-size: .9em;
  }
}

.list-module {
  background: $clr_6;
  padding: 15px;
  li {
    //border-bottom: 1px solid $clr_3;
    font-size: .85em;
    padding: 7px 0;
  }
}

.related-module {
  a {
    display: block;
  }
  img{
    &:hover {
      opacity: .7;
    }
  }
}

.modal {
  background: rgba(196, 248, 255, 0.5);
  .modal-content {
    border-color: rgba(196, 248, 255, 0.7);
  }
  
  .modal-body,
  .modal-header {
    padding: 30px;
  }
}

.close {
  color: $clr_4;
  transition: all .5s;
  font-size: 1.8em;
  &:focus,
  &:hover {
    color: $clr_4;
    transform: rotate(30deg);
  }
}

.tooltip-inner {
  background: $clr_4;
}

.tooltip.show {
  opacity: 1;
}

.tooltip.bs-tether-element-attached-left .tooltip-inner::before,
.tooltip.tooltip-right .tooltip-inner::before {
  border-right-color: $clr_4;
}

.tooltip.bs-tether-element-attached-top .tooltip-inner::before,
.tooltip.tooltip-bottom .tooltip-inner::before {
  border-bottom-color: $clr_4;
}

.tooltip.bs-tether-element-attached-bottom .tooltip-inner::before,
.tooltip.tooltip-top .tooltip-inner::before {
  border-top-color: $clr_4;
}

.tooltip.bs-tether-element-attached-right .tooltip-inner::before,
.tooltip.tooltip-left .tooltip-inner::before {
  border-left-color: $clr_4;
}

