// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {  }


@media (max-width: 768px) {
  .content {
    padding-bottom: 20px;
    .img-fluid {
      max-width: 60%;
      display: block;
      margin: 0 auto;
    }
  }
  .related-module {
    .img-fluid {
      max-width: 100%;
    }
  }
}
